import Vue from 'vue'
import Router from 'vue-router'

import Home from './components/home'
import inicio from './components/inicio'
import ini from './components/ini'
import Contenido from './components/Contenido.vue'
import Actividades from './components/Actividades.vue'
import act1 from './components/Actividades/act1'
import act2 from './components/Actividades/act2'
import act3 from './components/Actividades/act3'
import act4 from './components/Actividades/act4'
import Creditos from './components/Creditos.vue'
import gallery from './components/evaluacion.vue'
import complemento from './components/complemento.vue'
Vue.use(Router)


  const  routes = [
      {
         path: '/',
         name: 'inicio',
         component: inicio
      },
     {
        path: '/dashboard',
        name: 'home',
        component: Home,
        children: [
       {
        path: '/contenido',
        name: 'Contenido',
        component: Contenido
      },
      {
        path: '/ini',
        name: 'ini',
        component: ini
      },
      
      
      {
        path: '/Actividades',
        name: 'Actividades',
        component: Actividades,
        children: [
          {
            path: 'act1' ,
            name: 'act1' ,
            component: act1 ,
    
          },
          {
            path: 'act2' ,
            name: 'act2' ,
            component: act2 ,
            
          },
          {
            path: 'act3' ,
            name: 'act3' ,
            component: act3 ,
            
          },
          {
            path: 'act4' ,
            name: 'act4' ,
            component: act4 ,
            
          },
        ]},
      {
        path: '/team',
        name: 'team',
        component: Creditos
      },
      {
        path: '/gallery',
        name: 'gallery',
        component: gallery
      },
      {
        path: '/complemento',
        name: 'complemento',
        component: complemento
      },
 
  ]},
]; 
   const router = new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes
  });
  
  export default router;

